   max-width:73.8rem;
   padding-top:2rem;
   padding-bottom:2rem;
  

    } */
    

  @media screen and (max-width: 767px) {
    .container {
        max-width: 34.5rem;
        padding-top:2rem;
        padding-bottom:2rem;
       
    }
    .infoList{
        flex-wrap: wrap;
        justify-content: center;
        
    }
}
.infoList{
    display:flex;
    align-items:center ;
    justify-content: space-around;
}
.item{
    display:flex;
    gap:1.6rem;
}
.numberItem{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 2.8rem;
    line-height: 1.14;
    letter-spacing: -0.02em;
    color: var(--main-color);
}
.textItem{
   max-width:7.4rem;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 1.29;
    letter-spacing: -0.02em;
    color: rgba(18, 20, 23, 0.7);
}
